*{font-family:Quantico;}footer{width:100%;position:absolute;bottom:0;background-color:#27282F;}footer nav ul li{list-style:none;display:inline-block;margin:auto;position:relative;}footer nav ul li a{font-size:.5rem;text-decoration:none;color:white;font-family:Georgia,"Times New Roman",Times,serif;}.nav-footer{height:auto;width:100%;margin:auto;padding:0;display:flex;align-items:center;justify-content:space-between;}.icono{width:10px;height:auto;cursor:pointer;}@media (min-width:300px){footer{width:100%}}@media (max-width:480px){footer{position:absolute}footer nav ul li{display:inline-block;align-items:center}footer nav ul li a{font-size:.5rem;margin:auto;margin-right:.5rem}.icono{width:auto;height:8px}}@media (min-width:481px) and (max-width:779px){footer nav ul li{margin:.5rem}footer nav ul li a{font-size:.8rem}.img-logo{width:100%;height:10px}}@media (min-width:780px){footer nav ul li{margin:.5rem}footer nav ul li a{font-size:.8rem;margin-right:1rem}.img-logo{width:100%;height:10px;width:100%;height:50px}.icono{width:auto;height:15px}}@media (min-width:1200px){footer nav ul li{margin:auto}footer nav ul li a{font-size:1rem;margin-right:2rem}.img-logo{width:100%;height:50px}.icono{width:auto;height:15px}}@media (min-width:2000px){footer{position:absolute;bottom:0}footer nav ul li{margin:auto}footer nav ul li a{font-size:1.4rem;margin-right:2rem}.img-logo{width:100%;height:50px}.icono{width:auto;height:20px}}