* {
  font-family: Quantico;
}

footer {
  width: 100%;
  background-color: #27282f;
  position: absolute;
  bottom: 0;
}

footer nav ul li {
  margin: auto;
  list-style: none;
  display: inline-block;
  position: relative;
}

footer nav ul li a {
  color: #fff;
  font-family: Georgia, Times New Roman, Times, serif;
  font-size: .5rem;
  text-decoration: none;
}

.nav-footer {
  height: auto;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding: 0;
  display: flex;
}

.icono {
  width: 10px;
  height: auto;
  cursor: pointer;
}

@media (min-width: 300px) {
  footer {
    width: 100%;
  }
}

@media (max-width: 480px) {
  footer {
    position: absolute;
  }

  footer nav ul li {
    align-items: center;
    display: inline-block;
  }

  footer nav ul li a {
    margin: auto .5rem auto auto;
    font-size: .5rem;
  }

  .icono {
    width: auto;
    height: 8px;
  }
}

@media (min-width: 481px) and (max-width: 779px) {
  footer nav ul li {
    margin: .5rem;
  }

  footer nav ul li a {
    font-size: .8rem;
  }

  .img-logo {
    width: 100%;
    height: 10px;
  }
}

@media (min-width: 780px) {
  footer nav ul li {
    margin: .5rem;
  }

  footer nav ul li a {
    margin-right: 1rem;
    font-size: .8rem;
  }

  .img-logo {
    width: 100%;
    height: 10px;
    width: 100%;
    height: 50px;
  }

  .icono {
    width: auto;
    height: 15px;
  }
}

@media (min-width: 1200px) {
  footer nav ul li {
    margin: auto;
  }

  footer nav ul li a {
    margin-right: 2rem;
    font-size: 1rem;
  }

  .img-logo {
    width: 100%;
    height: 50px;
  }

  .icono {
    width: auto;
    height: 15px;
  }
}

@media (min-width: 2000px) {
  footer {
    position: absolute;
    bottom: 0;
  }

  footer nav ul li {
    margin: auto;
  }

  footer nav ul li a {
    margin-right: 2rem;
    font-size: 1.4rem;
  }

  .img-logo {
    width: 100%;
    height: 50px;
  }

  .icono {
    width: auto;
    height: 20px;
  }
}

